html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

body {
  background: #FFF;
  font-size: 50px;
  font-size: calc(16px + (50 - 16) * ((100vw - 600px) / (1000 - 600)));
  line-height: 1.4;
}

@media (max-width: 600px) {
  body {
    font-size: 16px;
  }
}

@media (min-width: 1000px) {
  body {
    font-size: 50px;
  }
}

.block {
  display: flex;
}

body {
  font-family: 'Open Sans', sans;
  color: #FFF;
}

.container {
  width: 1050px;
  margin: 0 auto;
}

.wrap {
  height: 931px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  position: relative;
}

.wrap:before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  height: 931px;
  background: rgba(0, 0, 0, 0.8);
}

.header {
  z-index: 1;
  margin-bottom: 120px;
}

.header .container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 100px;
  align-items: center;
  padding-top: 30px;
}

.logo {
  text-transform: uppercase;
  border: 3px solid #fff;
  line-height: .9;
  margin-right: 100px;
  font-size: 32px;
}

.logo__part1, .logo__part2 {
  padding: 7px;
  display: inline-block;
}

.logo__part1 {
  background: #FFF;
  color: #000;
  font-weight: bold;
  padding: 7px 10px;
}

.logo__part2 {
  padding-left: 0px;
  padding-right: 10px;
}

.header nav ul {
  font-size: 13px;
  text-transform: uppercase;
}

.header nav ul li {
  display: inline-block;
  margin-left: 45px;
  position: relative;
  box-sizing: border-box;
}

.header nav ul li a {
  font-family: 'Open Sans', sans;
  text-decoration: none;
  color: #FFF;
  border-bottom: 1px solid transparent;
}

.header nav ul li a:hover, .header nav ul li a:active {
  padding-bottom: 15px;
  border-bottom: 1px solid red;
  color: red;
  text-shadow: 0 0 1px red, 0 0 1px red, 0 0 1px red;
}

.section {
  margin: 0 auto;
  position: relative;
  flex: 1;
  text-align: center;
}

.section__suptitle {
  text-align: center;
  font-size: 18px;
  margin-bottom: 18px;
}

.section__title {
  margin: 0 auto;
  font-size: 63px;
  font-weight: bold;
  position: relative;
  display: inline-block;
  padding-bottom: 19px;
  margin-bottom: 50px;
}

.section__title:before, .section__title:after {
  width: 100px;
  height: 1px;
  background: #FFF;
  position: absolute;
  content: "";
  bottom: -8px;
}

.section__title:before {
  left: 30%;
}

.section__title:after {
  right: 30%;
}

.section__point {
  background: red;
  width: 5px;
  height: 5px;
  border-radius: 50%;
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translate(-50%, 0);
}

.section__text {
  width: 50%;
  font-size: 14px;
  margin: 0 auto;
  margin-bottom: 40px;
}

.section__btns {
  font-size: 14px;
  font-weight: bold;
  width: 430px;
  display: flex;
  margin: 0 auto;
  justify-content: space-between;
}

.section__btn {
  display: inline-block;
  line-height: 47px;
  border: 1px solid #fff;
  border-radius: 2px;
  width: 200px;
  cursor: pointer;
  text-decoration: none;
  color: #FFF;
  transition: .25s ease-out;
}

.section__btn:first-of-type {
  margin-right: 0;
}

.section__btn:hover {
  background: red;
  border: 1px solid red;
  border-radius: 5px;
}

.anchor {
  flex: none;
  position: relative;
  height: 100px;
  width: 100%;
  box-sizing: border-box;
}

.anchor:before, .anchor:after {
  content: '';
  background: #FFF;
  width: 40%;
  height: 80px;
  position: absolute;
  bottom: -20px;
  transform: skewY(3deg);
}

.anchor:after {
  right: 0;
  transform: skewY(-3deg);
}

.anchor__chamfer {
  transform: skewY(8deg);
  width: calc(50% + 50px);
  position: absolute;
  height: 100px;
  top: -15px;
  bottom: -50px;
  background: #fff;
  height: 100%;
  z-index: 2;
}

.anchor__chamfer-right {
  transform: rotate(0deg) skewY(-8deg);
  right: 0;
}

.anchor__img {
  position: relative;
  margin: 0 auto;
  width: 65px;
  height: 65px;
  margin-top: -4px;
}

.anchor__img:before {
  content: '';
  position: absolute;
  width: 73px;
  height: 73px;
  left: -4px;
  top: -4px;
  border-radius: 50%;
  background: rgba(204, 204, 204, 0.5);
}

.anchor__img-inside {
  position: absolute;
  width: 100%;
  height: 100%;
  background: #FFF;
  border-radius: 50%;
  z-index: 10;
}
